<template>
  <div class="records-container">
    <div class="header">
      <div class="back-button" @click="goBack">
        <el-icon><arrow-left /></el-icon> 
      </div>
      <h1></h1>
      <div class="home-link" @click="goHome" v-if="isAdmin">
         <el-icon><home /></el-icon>
      </div>
      <div v-else></div>
    </div>

    <div class="content">
      <div class="unit-info">
        <h2>{{ unitName }} - </h2>
      </div>
      
      <!--  -->
      <el-card class="filter-card">
        <div class="filter-header">
          <h3></h3>
          <el-button type="primary" link @click="showFilterPanel = !showFilterPanel">
            {{ showFilterPanel ? '' : '' }} 
            <el-icon v-if="!showFilterPanel"><arrow-down /></el-icon>
            <el-icon v-else><arrow-up /></el-icon>
          </el-button>
        </div>
        
        <div v-show="showFilterPanel" class="filter-form-container">
          <el-form :model="filterForm" label-width="100px" class="filter-form">
            <el-row :gutter="20">
              <!--  -->
              <el-col :span="12">
                <el-form-item label="">
                  <el-date-picker
                    v-model="filterForm.dateRange"
                    type="daterange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder=""
                    format="YYYY-MM-DD"
                    value-format="YYYY-MM-DD"
                    style="width: 100%"
                  />
                </el-form-item>
              </el-col>
              
              <!--  -->
              <el-col :span="12">
                <el-form-item label="">
                  <el-select v-model="filterForm.wasteTypeId" placeholder="" style="width: 100%" clearable>
                    <el-option 
                      v-for="type in wasteTypes" 
                      :key="type.id" 
                      :label="type.name" 
                      :value="type.id" 
                    />
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            
            <el-row :gutter="20">
              <!--  -->
              <el-col :span="12">
                <el-form-item label="()">
                  <div class="quantity-range">
                    <el-input-number 
                      v-model="filterForm.minQuantity" 
                      :min="0"
                      :precision="3"
                      :step="0.001"
                      placeholder=""
                      style="width: 48%"
                    />
                    <span class="range-separator"></span>
                    <el-input-number 
                      v-model="filterForm.maxQuantity" 
                      :min="filterForm.minQuantity || 0"
                      :precision="3"
                      :step="0.001"
                      placeholder=""
                      style="width: 48%"
                    />
                  </div>
                </el-form-item>
              </el-col>
              
              <!--  -->
              <el-col :span="12">
                <el-form-item label="">
                  <el-input 
                    v-model="filterForm.location" 
                    placeholder="" 
                    clearable
                  />
                </el-form-item>
              </el-col>
            </el-row>
            
            <div class="filter-actions">
              <el-button type="primary" @click="applyFilter"></el-button>
              <el-button @click="resetFilter"></el-button>
            </div>
          </el-form>
        </div>
      </el-card>
      
      <div class="records-wrapper">
        <el-card class="records-card">
          <div class="card-header">
          <h3></h3>
          <div class="card-actions">
          <el-button type="primary" @click="addNewRecord">
              <el-icon><plus /></el-icon> 
          </el-button>
          <el-button type="success" @click="goToUserManagement" v-if="isAdmin || isUnitAdmin">
              <el-icon><user /></el-icon> 
          </el-button>
          <el-button type="warning" @click="exportRecords">
              <el-icon><download /></el-icon> 
          </el-button>
          <el-button @click="refreshRecords">
            <el-icon><refresh /></el-icon> 
          </el-button>
        </div>
      </div>
          
          <el-table 
            :data="filteredRecords" 
            style="width: 100%" 
            border 
            v-loading="loading"
            stripe
          >
            <el-table-column prop="waste_type_name" label="" width="110" />
            <el-table-column prop="location" label="" />
            <el-table-column prop="collection_start_time" label="" width="160" />
            <el-table-column prop="quantity" label="()" width="100" />
            <el-table-column prop="created_at" label="" width="160" />
            <el-table-column label="" width="120">
              <template #default="scope">
                <div v-if="scope.row.photo_path_before">
                  <!--  -->
                  <div 
                    v-for="(path, index) in parsePhotoPath(scope.row.photo_path_before)" 
                    :key="index"
                    class="photo-thumbnail-container"
                    @click="previewPhoto(parsePhotoPath(scope.row.photo_path_before), index)"
                  >
                    <el-image 
                      :src="`${apiConfig.baseURL}${path}`"
                      fit="cover"
                      class="record-image"
                      :style="{ margin: index > 0 ? '2px 0 0 0' : '0' }"
                    >
                      <template #error>
                        <div class="image-error">
                          <el-icon><picture-failed /></el-icon>
                        </div>
                      </template>
                    </el-image>
                  </div>
                  <div v-if="parsePhotoPath(scope.row.photo_path_before).length > 1" class="photo-count">
                    {{ parsePhotoPath(scope.row.photo_path_before).length }}
                  </div>
                </div>
                <span v-else></span>
              </template>
            </el-table-column>
            <el-table-column label="" width="120">
              <template #default="scope">
                <div v-if="scope.row.photo_path_after">
                  <!--  -->
                  <div 
                    v-for="(path, index) in parsePhotoPath(scope.row.photo_path_after)" 
                    :key="index"
                    class="photo-thumbnail-container"
                    @click="previewPhoto(parsePhotoPath(scope.row.photo_path_after), index)"
                  >
                    <el-image 
                      :src="`${apiConfig.baseURL}${path}`"
                      fit="cover"
                      class="record-image"
                      :style="{ margin: index > 0 ? '2px 0 0 0' : '0' }"
                    >
                      <template #error>
                        <div class="image-error">
                          <el-icon><picture-failed /></el-icon>
                        </div>
                      </template>
                    </el-image>
                  </div>
                  <div v-if="parsePhotoPath(scope.row.photo_path_after).length > 1" class="photo-count">
                    {{ parsePhotoPath(scope.row.photo_path_after).length }}
                  </div>
                </div>
                <span v-else></span>
              </template>
            </el-table-column>
            <el-table-column label="" width="180">
              <template #default="scope">
                <el-button 
                  type="primary" 
                  size="small" 
                  @click="editRecord(scope.row.id)"
                  text
                  v-if="canEdit(scope.row)"
                >
                  
                </el-button>
                <el-button 
                  type="danger" 
                  size="small" 
                  @click="confirmDelete(scope.row)"
                  text
                  v-if="canEdit(scope.row)"
                >
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          
          <div class="empty-block" v-if="records.length === 0 && !loading">
            <el-empty description="" />
          </div>
        </el-card>
      </div>
    </div>

    <div class="footer">
      <p>&copy; 2025 </p>
    </div>

    <!--  -->
    <el-image-viewer
      v-if="showViewer"
      :url-list="previewImages"
      :initial-index="previewIndex"
      @close="closeViewer"
    />
  </div>
</template>

<script>
import { ref, onMounted, computed, reactive } from 'vue';
import { useRouter } from 'vue-router';
import { ElMessage, ElMessageBox, ElImageViewer } from 'element-plus';
import httpService from '../config/httpService';
import apiConfig from '../config/api';
import { ArrowLeft, Home, Refresh, PictureFailed, Plus, User, ArrowDown, ArrowUp, Download } from '@element-plus/icons-vue';
import { exportToExcel } from '../utils/exportUtils';
import auth from '../store/auth';

// JSON
const parsePhotoPath = (path) => {
  try {
    return JSON.parse(path);
  } catch (error) {
    console.error(':', error);
    return [];
  }
};

export default {
  name: 'RecordsList',
  components: {
    ArrowLeft,
    Home,
    Refresh,
    PictureFailed,
    Plus,
    User,
    ArrowDown,
    ArrowUp,
    Download,
    ElImageViewer
  },
  props: {
    unitId: {
      type: [String, Number],
      required: true
    }
  },
  setup(props) {
    const router = useRouter();
    const records = ref([]);
    const loading = ref(false);
    const unitName = ref('');
    const wasteTypes = ref([]);
    const showFilterPanel = ref(true);
    
    // 
    const filterForm = reactive({
      dateRange: null,
      wasteTypeId: null,
      minQuantity: null,
      maxQuantity: null,
      location: ''
    });
    
    // 
    const filteredRecords = computed(() => {
      return records.value.filter(record => {
        // 
        if (filterForm.wasteTypeId && record.waste_type_id !== filterForm.wasteTypeId) {
          return false;
        }
        
        // 
        if (filterForm.minQuantity !== null && parseFloat(record.quantity) < filterForm.minQuantity) {
          return false;
        }
        if (filterForm.maxQuantity !== null && parseFloat(record.quantity) > filterForm.maxQuantity) {
          return false;
        }
        
        // 
        if (filterForm.location && !record.location.toLowerCase().includes(filterForm.location.toLowerCase())) {
          return false;
        }
        
        // 
        if (filterForm.dateRange && filterForm.dateRange.length === 2) {
          const startDate = new Date(filterForm.dateRange[0]);
          // 23:59:59
          const endDate = new Date(filterForm.dateRange[1]);
          endDate.setHours(23, 59, 59, 999);
          
          // 
          if (record.collection_start_time) {
            // 
            const recordDate = parseFormattedDateTime(record.collection_start_time);
            if (recordDate < startDate || recordDate > endDate) {
              return false;
            }
          } else {
            // 
            return false;
          }
        }
        
        return true;
      });
    });
    
    // 
    const parseFormattedDateTime = (formattedDateTime) => {
      //  "2025-03-01 14:30:00"
      // 
      const cleanedStr = formattedDateTime.replace(/[^0-9\-: ]/g, '');
      return new Date(cleanedStr);
    };
    
    // 
    const isAdmin = computed(() => {
      return auth.state.isLoggedIn && auth.state.user.role_id === 3;
    });
    
    // 
    const isUnitAdmin = computed(() => {
      return auth.state.isLoggedIn && auth.state.user.role_id === 2;
    });

    // 
    const showViewer = ref(false);
    const previewImages = ref([]);
    const previewIndex = ref(0);
    
    // 
    const previewPhoto = (paths, index) => {
      previewImages.value = paths.map(path => `${apiConfig.baseURL}${path}`);
      previewIndex.value = index;
      showViewer.value = true;
    };
    
    // 
    const closeViewer = () => {
      showViewer.value = false;
    };

    onMounted(async () => {
      await fetchUnitName();
      await fetchWasteTypes();
      await fetchRecords();
    });

    // 
    const fetchWasteTypes = async () => {
      try {
        const response = await httpService.get(apiConfig.endpoints.wasteTypes);
        wasteTypes.value = response.data;
      } catch (error) {
        console.error('Error fetching waste types:', error);
        ElMessage.error('');
      }
    };
    
    const fetchUnitName = async () => {
      try {
        const response = await httpService.get(apiConfig.endpoints.units);
        const unit = response.data.find(u => u.id === parseInt(props.unitId));
        if (unit) {
          unitName.value = unit.name;
        }
      } catch (error) {
        console.error('Error fetching unit name:', error);
        ElMessage.error('');
      }
    };

    const fetchRecords = async () => {
      loading.value = true;
      try {
        // ID
        const response = await httpService.get(`${apiConfig.endpoints.wasteRecords}/user/${auth.state.user.id}`);
        records.value = response.data;
        
        // 
        records.value.forEach(record => {
          record.created_at = formatDateTime(record.created_at);
          record.collection_start_time = formatDateTime(record.collection_start_time);
        });
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      } finally {
        loading.value = false;
      }
    };

    const formatDateTime = (dateTimeStr) => {
      if (!dateTimeStr) return '';
      const date = new Date(dateTimeStr);
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
        hour12: false
      });
    };

    const refreshRecords = () => {
      fetchRecords();
    };

    const goBack = () => {
      router.push({ name: 'WasteForm', params: { id: props.unitId } });
    };

    // 
    const goHome = () => {
      // 
      if (isAdmin.value) {
        router.push('/');
      }
    };

    // 
    const goToUserManagement = () => {
      router.push('/user-management');
    };

    // 
    const addNewRecord = () => {
      router.push('/record/new');
    };

    // 
    const editRecord = (recordId) => {
      router.push(`/record/${recordId}`);
    };

    // 
    // 
    // 
    // 
    const canEdit = (record) => {
      if (!auth.state.isLoggedIn) return false;

      if (auth.state.user.role_id === 3) return true; // 
      
      if (auth.state.user.role_id === 2 && auth.state.user.unit_id === record.unit_id) return true; // 
      
      // 
      // : creator_id  null  null 
      return auth.state.user.unit_id === record.unit_id && 
             (record.creator_id === null || record.creator_id === auth.state.user.id);
    };

    // 
    const applyFilter = () => {
      //  computed 
      ElMessage.success('');
    };
    
    // 
    const resetFilter = () => {
      filterForm.dateRange = null;
      filterForm.wasteTypeId = null;
      filterForm.minQuantity = null;
      filterForm.maxQuantity = null;
      filterForm.location = '';
      ElMessage.info('');
    };
    
    // Excel
    const exportRecords = () => {
      if (filteredRecords.value.length === 0) {
        ElMessage.warning('');
        return;
      }
      
      // 
      const exportHeaders = [
        { title: '', field: 'unit_name', width: 15 },
        { title: '', field: 'waste_type_name', width: 15 },
        { title: '', field: 'location', width: 20 },
        { title: '', field: 'collection_start_time', width: 20, type: 'datetime' },
        { title: '()', field: 'quantity', width: 12, type: 'number' },
        { title: '', field: 'created_at', width: 20, type: 'datetime' }
      ];
      
      // 
      let fileName = `${unitName.value}_`;
      
      // 
      if (filterForm.wasteTypeId) {
        const wasteType = wasteTypes.value.find(type => type.id === filterForm.wasteTypeId);
        if (wasteType) {
          fileName += `_${wasteType.name}`;
        }
      }
      
      // 
      exportToExcel(filteredRecords.value, fileName, exportHeaders);
      ElMessage.success('');
    };
    
    const confirmDelete = (record) => {
      ElMessageBox.confirm(
        '',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      )
        .then(async () => {
          try {
            await httpService.delete(`${apiConfig.endpoints.wasteRecords}/${record.id}`);
            ElMessage.success('');
            await fetchRecords();
          } catch (error) {
            console.error(':', error);
            ElMessage.error('');
          }
        })
        .catch(() => {
          ElMessage.info('');
        });
    };

    return {
      records,
      filteredRecords,
      loading,
      unitName,
      wasteTypes,
      isAdmin,
      isUnitAdmin,
      parsePhotoPath,
      refreshRecords,
      goBack,
      goHome,
      goToUserManagement,
      addNewRecord,
      editRecord,
      canEdit,
      confirmDelete,
      apiConfig,
      // 
      showFilterPanel,
      filterForm,
      applyFilter,
      resetFilter,
      // 
      exportRecords,
      // 
      showViewer,
      previewImages,
      previewIndex,
      previewPhoto,
      closeViewer
    };
  }
};
</script>

<style scoped>
.records-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.header {
  background-color: #409EFF;
  color: white;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.back-button, .home-link {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 14px;
}

.content {
  flex: 1;
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
}

.unit-info {
  text-align: center;
  margin-bottom: 20px;
}

.unit-info h2 {
  color: #333;
  border-bottom: 2px solid #409EFF;
  display: inline-block;
  padding-bottom: 5px;
}

.records-wrapper {
  margin-top: 20px;
}

.records-card {
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.card-header h3 {
  margin: 0;
  color: #333;
}

.record-image {
  width: 50px;
  height: 50px;
  border-radius: 4px;
  cursor: pointer;
  display: block;
}

.photo-count {
  font-size: 12px;
  color: #409EFF;
  text-align: center;
  margin-top: 2px;
}

.image-error {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  background-color: #f5f7fa;
  color: #909399;
}

.empty-block {
  margin: 20px 0;
}

.filter-card {
  margin-bottom: 20px;
}

.filter-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.filter-header h3 {
  margin: 0;
  color: #333;
}

.filter-form-container {
  transition: all 0.3s ease;
}

.quantity-range {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.range-separator {
  margin: 0 5px;
}

.filter-actions {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  gap: 10px;
}

.footer {
  background-color: #f5f5f5;
  padding: 15px;
  text-align: center;
  color: #666;
}

.photo-thumbnail-container {
  cursor: pointer;
  display: inline-block;
}
</style>

<style>
/* Element Plusz-index */
.el-image-viewer__wrapper {
  z-index: 2147483647 !important; /* z-index */
  position: fixed !important;
}

/*  */
.el-image-viewer__mask {
  z-index: 2147483646 !important;
  position: fixed !important;
}

/*  */
.el-image-viewer__btn {
  z-index: 2147483647 !important;
  position: fixed !important;
}

/*  */
.el-image-viewer__close {
  z-index: 2147483647 !important;
  position: fixed !important;
}

/*  */
.el-image-viewer__img {
  z-index: 2147483646 !important;
  position: relative !important;
}

/*  */
.el-image-viewer__actions {
  z-index: 2147483647 !important;
  position: fixed !important;
}

/*  */
.el-image-viewer__actions__inner {
  z-index: 2147483647 !important;
  position: relative !important;
}
</style>
