// API 

//  API  URL
//  API
// Nginx
const API_BASE_URL = '';

export default {
  baseURL: API_BASE_URL,
  
  // API 
  endpoints: {
    login: '/api/login',
    users: '/api/users',
    units: '/api/units',
    wasteTypes: '/api/waste-types',
    wasteRecords: '/api/waste-records',
  },
  
  //  API URL
  getUrl(endpoint) {
    return `${this.baseURL}${endpoint}`;
  }
};
