<template>
  <div class="user-management-container">
    <div class="header">
      <div class="back-button" @click="goBack">
        <el-icon><arrow-left /></el-icon> 
      </div>
      <h1></h1>
      <div></div>
    </div>

    <div class="content">
      <div class="toolbar">
        <el-button type="primary" @click="openAddDialog">
          <el-icon><plus /></el-icon> 
        </el-button>
      </div>

      <el-table
        v-loading="loading"
        :data="users"
        border
        stripe
        style="width: 100%; margin-top: 20px;"
      >
        <el-table-column prop="id" label="ID" width="60" />
        <el-table-column prop="username" label="" width="120" />
        <el-table-column prop="phone" label="" width="140" />
        <el-table-column prop="role_name" label="" width="120" />
        <el-table-column prop="unit_name" label="" min-width="120" />
        <el-table-column label="" width="180" fixed="right">
          <template #default="scope">
            <el-button size="small" @click="handleEdit(scope.row)"></el-button>
            <el-popconfirm
              title=""
              @confirm="handleDelete(scope.row)"
            >
              <template #reference>
                <el-button size="small" type="danger"></el-button>
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- / -->
    <el-dialog
      v-model="dialogVisible"
      :title="isEdit ? '' : ''"
      width="500px"
    >
      <el-form
        ref="userForm"
        :model="form"
        :rules="rules"
        label-width="100px"
        style="max-width: 400px; margin: 0 auto;"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="form.username" placeholder="" />
        </el-form-item>
        
        <el-form-item label="" prop="phone">
          <el-input v-model="form.phone" placeholder="" />
        </el-form-item>
        
        <el-form-item label="" prop="roleId">
          <el-select v-model="form.roleId" placeholder="" style="width: 100%">
            <!--  -->
            <el-option v-if="isSuperAdmin" :value="3" label="" />
            <el-option :value="2" label="" />
            <el-option :value="1" label="" />
          </el-select>
        </el-form-item>
        
        <el-form-item label="" prop="unitId" v-if="form.roleId !== 3">
          <el-select 
            v-model="form.unitId" 
            placeholder="" 
            style="width: 100%"
            :disabled="!isSuperAdmin && isEdit"
          >
            <el-option 
              v-for="unit in units" 
              :key="unit.id" 
              :label="unit.name" 
              :value="unit.id" 
            />
          </el-select>
        </el-form-item>
        
        <el-form-item 
          label="" 
          prop="password" 
          v-if="form.roleId === 2 || form.roleId === 3"
        >
          <el-input 
            v-model="form.password" 
            placeholder="" 
            type="password"
            show-password
          />
          <div class="password-hint" v-if="!isEdit"></div>
          <div class="password-hint" v-else></div>
        </el-form-item>
      </el-form>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="submitForm" :loading="formLoading">
            
          </el-button>
        </span>
      </template>
    </el-dialog>

    <div class="footer">
      <p>&copy; 2025 </p>
    </div>
  </div>
</template>

<script>
import { ref, reactive, onMounted, computed } from 'vue';
import { useRouter } from 'vue-router';
import { ElMessage } from 'element-plus';
import httpService from '../config/httpService';
import apiConfig from '../config/api';
import { ArrowLeft, Plus } from '@element-plus/icons-vue';
import auth from '../store/auth';

export default {
  name: 'UserManagement',
  components: {
    ArrowLeft,
    Plus
  },
  setup() {
    const router = useRouter();
    const loading = ref(false);
    const formLoading = ref(false);
    const dialogVisible = ref(false);
    const isEdit = ref(false);
    const userForm = ref(null);
    const users = ref([]);
    const units = ref([]);
    
    // 
    const isSuperAdmin = computed(() => {
      return auth.state.isLoggedIn && auth.state.user.role_id === 3;
    });
    
    // ID
    const currentUnitId = computed(() => {
      return auth.state.isLoggedIn ? auth.state.user.unit_id : null;
    });
    
    // 
    const form = reactive({
      id: null,
      username: '',
      phone: '',
      roleId: 1,
      unitId: null,
      password: ''
    });
    
    // 
    const rules = {
      username: [
        { required: true, message: '', trigger: 'blur' },
        { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
      ],
      phone: [
        { required: true, message: '', trigger: 'blur' },
        { pattern: /^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
      ],
      roleId: [
        { required: true, message: '', trigger: 'change' }
      ],
      unitId: [
        { required: true, message: '', trigger: 'change' }
      ],
      password: [
        { 
          required: function() {
            // 
            return !isEdit.value && (form.roleId === 2 || form.roleId === 3);
          }, 
          message: '', 
          trigger: 'blur' 
        },
        { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
      ]
    };
    
    // 
    const fetchUsers = async () => {
      loading.value = true;
      try {
        let response;
        if (isSuperAdmin.value) {
          // 
          response = await httpService.get(apiConfig.endpoints.users);
        } else {
          // 
          response = await httpService.get(`${apiConfig.endpoints.units}/${currentUnitId.value}/users`);
        }
        users.value = response.data;
      } catch (error) {
        console.error('Error fetching users:', error);
        ElMessage.error('');
      } finally {
        loading.value = false;
      }
    };
    
    // 
    const fetchUnits = async () => {
      try {
        const response = await httpService.get(apiConfig.endpoints.units);
        units.value = response.data;
      } catch (error) {
        console.error('Error fetching units:', error);
        ElMessage.error('');
      }
    };
    
    // 
    const openAddDialog = () => {
      isEdit.value = false;
      resetForm();
      dialogVisible.value = true;
      
      // 
      if (!isSuperAdmin.value) {
        form.unitId = currentUnitId.value;
      }
    };
    
    // 
    const handleEdit = (row) => {
      isEdit.value = true;
      form.id = row.id;
      form.username = row.username;
      form.phone = row.phone;
      form.roleId = row.role_id;
      form.unitId = row.unit_id;
      form.password = ''; // 
      dialogVisible.value = true;
    };
    
    // 
    const handleDelete = async (row) => {
      try {
        await httpService.delete(`${apiConfig.endpoints.users}/${row.id}`);
        ElMessage.success('');
        fetchUsers();
      } catch (error) {
        console.error('Error deleting user:', error);
        let errorMsg = '';
        if (error.response && error.response.data && error.response.data.error) {
          errorMsg = error.response.data.error;
        }
        ElMessage.error(errorMsg);
      }
    };
    
    // 
    const resetForm = () => {
      if (userForm.value) {
        userForm.value.resetFields();
      }
      form.id = null;
      form.username = '';
      form.phone = '';
      form.roleId = 1;
      form.unitId = null;
      form.password = '';
    };
    
    // 
    const submitForm = () => {
      userForm.value.validate(async (valid) => {
        if (valid) {
          formLoading.value = true;
          try {
            const userData = {
              username: form.username,
              phone: form.phone,
              roleId: form.roleId,
              unitId: form.roleId !== 3 ? form.unitId : null
            };
            
            // 
            if (form.password) {
              userData.password = form.password;
            }
            
            if (isEdit.value) {
              // 
              await httpService.put(`${apiConfig.endpoints.users}/${form.id}`, userData);
              ElMessage.success('');
            } else {
              // 
              await httpService.post(apiConfig.endpoints.users, userData);
              ElMessage.success('');
            }
            
            dialogVisible.value = false;
            fetchUsers();
          } catch (error) {
            console.error('Error submitting user form:', error);
            let errorMsg = isEdit.value ? '' : '';
            if (error.response && error.response.data && error.response.data.error) {
              errorMsg = error.response.data.error;
            }
            ElMessage.error(errorMsg);
          } finally {
            formLoading.value = false;
          }
        } else {
          ElMessage.warning('');
        }
      });
    };
    
    // 
    const goBack = () => {
      router.back();
    };
    
    onMounted(() => {
      fetchUsers();
      fetchUnits();
    });
    
    return {
      loading,
      formLoading,
      dialogVisible,
      isEdit,
      userForm,
      users,
      units,
      form,
      rules,
      isSuperAdmin,
      currentUnitId,
      openAddDialog,
      handleEdit,
      handleDelete,
      submitForm,
      goBack
    };
  }
};
</script>

<style scoped>
.user-management-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.header {
  background-color: #409EFF;
  color: white;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.back-button {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 14px;
}

.content {
  flex: 1;
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
}

.toolbar {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

.password-hint {
  font-size: 12px;
  color: #999;
  margin-top: 5px;
}

.footer {
  background-color: #f5f5f5;
  padding: 15px;
  text-align: center;
  color: #666;
}
</style>
