// CSV

/**
 * CSV
 * @param {Array} data - 
 * @param {String} fileName - 
 * @param {Array} headers - 
 */
export const exportToExcel = (data, fileName, headers) => {
  // 
  if (!data || data.length === 0) {
    return;
  }

  // 
  const safeFileName = fileName || '';
  const fileNameWithExtension = `${safeFileName}_${new Date().toISOString().slice(0, 10)}.csv`;

  // CSV
  let csvContent = '\uFEFF'; // BOMExcel
  
  // 
  const headerRow = headers.map(header => header.title).join(',');
  csvContent += headerRow + '\n';

  // 
  data.forEach(item => {
    const row = headers.map(header => {
      let value = item[header.field];
      
      // nullundefined
      if (value === null || value === undefined) {
        return '';
      }
      
      // 
      if (header.type === 'number') {
        return value;
      }
      
      // 
      value = String(value);
      if (value.includes(',') || value.includes('"') || value.includes('\n')) {
        return `"${value.replace(/"/g, '""')}"`;
      }
      
      return value;
    }).join(',');
    
    csvContent += row + '\n';
  });

  // 
  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
  const link = document.createElement('a');
  
  // URL.createObjectURLURL
  const url = URL.createObjectURL(blob);
  link.setAttribute('href', url);
  link.setAttribute('download', fileNameWithExtension);
  link.style.visibility = 'hidden';
  
  // 
  document.body.appendChild(link);
  link.click();
  
  // 
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
};
